@import url(https://fonts.googleapis.com/css2?family=Baloo+Chettan+2:wght@400;500;600;700;800&family=Balsamiq+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
*{font-family:"Baloo Chettan 2", cursive;box-sizing:border-box;color:white}::-webkit-scrollbar{display:none}body{margin:0;height:100vh}

h2,h1,p{margin:0}.current__container{width:100%;height:100vh;display:flex;flex-direction:column;justify-content:space-between;background-image:url(/climApp/static/media/thunder.e4f16078.jpg);background-size:cover}.current{position:relative;top:-100px;display:flex;justify-content:flex-start;align-items:center;justify-items:center;align-content:center;margin-left:20px}.current__details{margin-right:20px;margin-left:20px;text-align:center}.current__details h2{font-size:40px}.current__details p{font-size:20px}.current__icon{width:70px}

.side{position:absolute;width:33%;top:0;right:0;height:100vh;padding:20px;-webkit-backdrop-filter:blur(50px);backdrop-filter:blur(50px);display:flex;flex-direction:column;justify-content:center}.side hr{width:100%}.side h1,.side h2,.side h3{color:rgba(255,255,255,0.5)}.weather{overflow:scroll}.weather__item{display:flex;align-items:center;justify-content:space-between}.nearby__item{display:flex;justify-content:space-between;align-items:center}.nearby img{width:60px}.nearby__item--details{display:flex;align-items:center}

